<template>
    <div class="app-container" :style="background" style="height: 3000px;width: 100%;margin: 5px auto 0 auto">
      <br>
      <div style="text-align:center"><span style="color:white;font-size: 50px"><strong>ID:{{accident.id}}</strong></span></div>
      <div style="display: flex;margin-top: 15px">
        <div style="margin-left: 4%;width: 35%;">
          <br>
          <div class="circle" style="float: left;"></div>
          <div style="font-family: 'Roboto Slab', serif; color: white">
            <span style="font-size: 33px;font-weight: bold">&nbsp;测试系统:&nbsp;&nbsp;</span>
            <span style="font-size: 30px">{{accident.system.type}}</span>
          </div>
          <div class="circle" style="float: left;"></div>
          <div style="font-family: 'Roboto Slab', serif; color: white">
            <span style="font-size: 33px;font-weight: bold">&nbsp;事故类型:&nbsp;&nbsp;</span>
            <span style="font-size: 30px">{{accident.targetAccidents.type}}</span>
          </div>
          <div class="circle" style="float: left;"></div>
          <div style="font-family: 'Roboto Slab', serif; color: white">
            <span style="font-size: 33px;font-weight: bold">&nbsp;事故相关车辆:&nbsp;&nbsp;</span>
            <span style="font-size: 30px">{{accident.vehicle}}</span>
          </div>
          <div class="circle" style="float: left;"></div>
          <div style="font-family: 'Roboto Slab', serif; color: white">
            <span style="font-size: 33px;font-weight: bold">&nbsp;问题模块:&nbsp;&nbsp;</span>
            <span style="font-size: 30px">{{accident.faultyModule}}</span>
          </div>
          <div class="circle" style="float: left;"></div>
          <div style="font-family: 'Roboto Slab', serif; color: white">
            <span style="font-size: 33px;font-weight: bold">&nbsp;无人车规划任务:&nbsp;&nbsp;</span>
            <span style="font-size: 30px">{{accident.taskPlanning.task}}</span>
          </div>
          <div class="circle" style="float: left;"></div>
          <div style="font-family: 'Roboto Slab', serif; color: white">
            <span style="font-size: 33px;font-weight: bold">&nbsp;车道类型:&nbsp;&nbsp;</span>
            <span style="font-size: 30px">{{accident.drivewayType.type}}</span>
          </div>
          <div class="circle" style="float: left;"></div>
          <div style="font-family: 'Roboto Slab', serif; color: white">
            <span style="font-size: 33px;font-weight: bold">&nbsp;障碍物类型:&nbsp;&nbsp;</span>
            <span style="font-size: 30px">{{accident.obstacleType.type}}</span>
          </div>
          <div class="circle" style="float: left;"></div>
          <div style="font-family: 'Roboto Slab', serif; color: white">
            <span style="font-size: 33px;font-weight: bold">&nbsp;障碍物数量:&nbsp;&nbsp;</span>
            <span style="font-size: 30px">{{accident.num}}</span>
          </div>
          <br>
          <br>
          <br>
          <router-link class="accident-link" :to="{path:'/accidentAnalysis',query:{showDialog: true}}">
            <button style="margin-left: 30px">
            <span style="font-size: 35px;color:  #97c2ff;font-weight: bold">事故分析</span>
            </button>
          </router-link>
          <div  class="button" style="margin-left: 30px">
            <div class="button-wrapper" @click="downloadJson">
              <div class="text">下载</div>
              <span class="icon">
                <svg viewBox="0 0 24 24" preserveAspectRatio="xMidYMid meet" height="2em" width="2em" role="img" aria-hidden="true" xmlns="http://www.w3.org/2000/svg">
                  <path d="M12 15V3m0 12l-4-4m4 4l4-4M2 17l.621 2.485A2 2 0 0 0 4.561 21h14.878a2 2 0 0 0 1.94-1.515L22 17"
                        stroke-width="2" stroke-linejoin="round" stroke-linecap="round" stroke="currentColor" fill="none"></path></svg>
              </span>
            </div>
          </div>
            </div>
          <div>
              <video controls width="90%" :src="accident.videoUrl" type="video/mp4" class="cardvideo" ></video>
          </div>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'AccidentDetails',
    data () {
      return {
        accident: '',
        background: {
          width: '100%',
          height: '782px',
          backgroundImage: 'url(' + require('@/assets/img/bg/背景3.png') + ')',
          backgroundRepeat: 'no-repeat',
          backgroundSize: '100% 100%'
        }
      }
    },
    mounted () {
      this.loadAccident()
    },
    methods: {
      loadAccident () {
        var _this = this
        this.$axios.get('/accidents/' + this.$route.query.id).then(resp => {
        if (resp && resp.data.code === 200) {
          _this.accident = resp.data.result
          }
        })
      },
      downloadJson () {
        let _this = this
        this.$axios.get('/accidents/download/' + this.accident.id, {
          responseType: 'blob'
        }).then(resp => {
          const blob = new Blob([resp.data], {
            type: 'application/json'
          })
          const link = document.createElement('a')
          link.href = window.URL.createObjectURL(blob) // 指定下载链接
          link.setAttribute('download', _this.accident.fileName) // 指定下载文件名
          document.body.appendChild(link)
          link.click()
          window.URL.revokeObjectURL(link.href) // 释放URL对象
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/markdown.css";
  .video {
    border-radius: 1.45em;
  }
  .circle {
    background: white;
    clip-path: circle(40%);
    height: 30px;
    width: 30px;

  }
  .button {
    --width: 230px;
    --height: 70px;
    --tooltip-height: 35px;
    --tooltip-width: 90px;
    --gap-between-tooltip-to-button: 18px;
    --button-color: #97c2ff;
    width: var(--width);
    height: var(--height);
    background: var(--button-color);
    position: relative;
    text-align: center;
    border-radius: 1.45em;
    font-family: "Arial";
    transition: background 0.3s;
  }
  .button::before {
    position: absolute;
    content: attr(data-tooltip);
    width: var(--tooltip-width);
    height: var(--tooltip-height);
    font-size: 0.9rem;
    color: #111;
    border-radius: .25em;
    line-height: var(--tooltip-height);
    bottom: calc(var(--height) + var(--gap-between-tooltip-to-button) + 10px);
    left: calc(50% - var(--tooltip-width) / 2);
  }

  .button::after {
    position: absolute;
    content: '';
    width: 0;
    height: 0;
    border: 10px solid transparent;
    left: calc(50% - 10px);
    bottom: calc(100% + var(--gap-between-tooltip-to-button) - 10px);
  }

  .button::after,.button::before {
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s;
  }

  .text {
    font-size: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
  }

  .button-wrapper,.text,.icon {
    overflow: hidden;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    color: #fff;
  }

  .text {
    top: 0
  }

  .text,.icon {
    transition: top 0.5s;
  }

  .icon {
    color: #fff;
    top: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .button:hover {
    background: #27408B;
  }

  .button:hover .text {
    top: -100%;
  }

  .button:hover .icon {
    top: 0;
  }

  .button:hover:before,.button:hover:after {
    opacity: 1;
    visibility: visible;
  }

  .button:hover:after {
    bottom: calc(var(--height) + var(--gap-between-tooltip-to-button) - 20px);
  }

  .button:hover:before {
    bottom: calc(var(--height) + var(--gap-between-tooltip-to-button));
  }
  button {
    display: inline-block;
    background-color: #FFFFFF;
    border: none;
    text-align: center;
    font-size: 17px;
    padding: 16px;
    width: 230px;
    transition: all 0.5s;
    cursor: pointer;
    height: 70px;
    position: relative;
    border-radius: 1.45em;
    font-family: "Arial";
  }

  button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }

  button span:after {
    content: '»';
    text-size-adjust: auto;
    position: absolute;
    opacity: 0;
    top: 0;
    right: -5px;
    transition: 0.5s;
  }

  button:hover span {
    padding-right: 15px;
  }

  button:hover span:after {
    opacity: 1;
    right: -20px;
  }
  * {
    margin: 0;
    padding: 0;
    font-family: '微软雅黑', Arial, Helvetica, sans-serif;
    box-sizing: border-box;
    list-style: none;
  }
  .head li{
    margin: 0;
    height: 50px;
  }
  .cardvideo{
    margin: 30px 50px;
    height: 580px;
    border-radius: 1.45em;
    border:8px solid white;
  }
</style>



